#home{
	background-color:#0FB2D4; 
	padding-bottom: 7rem;
}
#home a{
	color:white;
	font-weight:bold;
	position:relative;
}
#home a.nav-link:after{
    content: "";
    background-color: #232d37;
    height: 2px;
    width: 0;
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    visibility:hidden;
    opacity:0;
    transition: all 1s ease-in-out 0s;
    margin:0 auto;
}
#home a.nav-link:hover:after{
	width: 50px;
	opacity:1;
	visibility:visible;
	transition: all 1s ease-in-out 0s;
}
#home a:hover{
	color: #232d37;
}
#home img{
	width:200px;
	height:200px;
	transition: all 1s ease-in-out 0s;
}
#home img:hover{
	filter: grayscale(1);
	transition: all 1s ease-in-out 0s;
}
#home h1{
	font-weight:bold;
}
#home .social li a{
    width: 45px;
    height: 45px;
    border: 2px solid #fff;
    font-size: 22px;
    display: block;
    border-radius: 50%;
    line-height: 38px;
    transition: all .4s ease-out;
}
#home .social li a:hover{
	border: 2px solid transparent;
	transform: rotateZ(360deg);
	transition: -webkit-transform .4s ease-out;
	color:#fff;
}
#home .social li:first-child a:hover{
	background: #3b5998;
}
#home .social li:nth-child(2) a:hover{
	background: #8a3ab9;
}
#home .social li:nth-child(3) a:hover{
	background: #0077b5;
}
#home .social li:last-child a:hover{
	background: #3f729b;
}
@media(max-width:991px){
	#home a.nav-link{
		padding:.5rem 0.5rem;
	}
}
.toggle{
	display:none;
	position: absolute;
    right: 25px;
    top: 0px;
    width: 40px;
    z-index: 9999;
    background: transparent;
    cursor: pointer;
    padding: 18px 11px 18px 0px;
    transition: padding-right 0.3s ease;
    -webkit-transition: padding-right 0.3s ease;
    -ms-transition: padding-right 0.3s ease;
}
.toggle span {
    display: block;
    position: relative;
    background: #000000;
    height: 1px;
    transition: background 0.3s 0.3s ease;
    -webkit-transition: background 0.3s 0.3s ease;
}
.toggle span:before,
.toggle span:after {
    content: '';
    position: absolute;
    left: 0;
    width: 34px;
    height: 1px;
    background: #000000;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -ms-transition: 0.3s;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    -webkit-transition: top 0.3s 0.3s ease, -webkit-transform 0.3s ease, width 0.3s ease, background 0.3s 0.3s ease;
    transition: top 0.3s 0.3s ease, transform 0.3s ease, width 0.3s ease, background 0.3s 0.3s ease;
}
.toggle span:after {
    top: -11px;
}
.toggle span:before {
    top: 11px;
    width: 24px;
}
.toggle:hover{
    padding-right: 6px;
}
.toggle:hover span:before{
    width: 34px;
}
@media(max-width:576px){
	.toggle{
		display:block;
	}
    .menu-list{
        display:none;
    }
    .open.menu-list{
        display:block;
    }
	.menu-list li{
		display:block !important;
	}
	.menu-list{
		position: absolute;
		right: 30px;
		text-align: right;
		top: 40px;
		background-color: rgba(0,0,0,0.8);
		padding: 10px;
	}
	#home a.nav-link{
		padding:0.3rem 0.5rem;
	}
	#home a.nav-link:hover{
		color:#fff;
	}
	#home a.nav-link:after{
		display:none;
	}
    .toggle.open span:before{
        width: 30px;
        transform: rotate(145deg);
        top: 0;
    }
    .toggle.open span:after {
        top: 0;
        width: 30px;
        transform: rotate(-145deg);
    }
    .toggle.open span{
        background:none;
    }
}