.homeTransition-appear{
  opacity: 0;
}
.homeTransition-appear.homeTransition-appear-active{
  opacity: 1;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}
.worksTransition-appear{
  opacity: 0;
  -webkit-transform: translateX(-100px);
          transform: translateX(-100px);
}
.worksTransition-appear.worksTransition-appear-active{
  opacity: 1;
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}
.aboutTransition-appear{
  opacity: 0;
}
.aboutTransition-appear.aboutTransition-appear-active{
  opacity: 1;
  color: red;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}
