#portfolio .container{
	background-color: #fff;
    margin-top: -50px;
    border-radius: 20px;
    padding-top: 25px;
}
#portfolio figure{
	position:relative;
}
#portfolio h4{
    font-weight:bold;
}
#portfolio figure img{
	display: block;
    z-index: 10;
    transition: all .5s;
    position:relative;
}
#portfolio figure:hover img{
    transform: rotate(-360deg) scale(0);
    transition: all .5s;
}
#portfolio figcaption{
	width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    background: #0FB2D4;
    position: absolute;
}
#portfolio figcaption a{
	color:#fff;
	display: block;
    width: 100%;
    height: 100%;
    padding: 20px;
}
#portfolio figcaption a:hover{
	text-decoration:none;
}