#Contact{
	background-color: #3c4854;
}
#Contact h4{
	font-weight:bold;
	color:#fff;
}
#Contact .info svg{
	font-size: 24px;
    margin-bottom: 15px;
    margin-top: 20px;
}
#Contact .info h6{
	font-weight: bold;
	margin-bottom: 10px;
}
#Contact form{
	margin-top:60px;
}
#Contact form input,
#Contact form textarea{
	background-color:transparent;
	border:2px solid transparent;
	border-radius:0;
	box-shadow:none;
	color:#fff;
	border-bottom-color:#fff;
}
#Contact form input:focus,
#Contact form textarea:focus{
	border-color:#fff;
}
#Contact form button{
	background-color: #212529;
    border-color: #212529;
}
#Contact form button:hover{
	background-color:transparent;
}
#Contact label{
	color:#fff;
}
@media(max-width:767px){
	#Contact form{
		width:100% !important;
	}
}