#experience{
    background-color:#3c4854;
}
#experience h4{
    color: #fff;
}
#experience h4,
#experience .well h5{
	font-weight:bold;
}
#experience .well{
	background: #fff;
    border: 1px solid #e3e3e3;
    box-shadow: 4px 3px 3px #333;
    padding: 20px;
    border-radius: 4px;
    transition: all 1s ease-in-out 0s;
    width:85%;
}
#experience .well:hover{
	transition: all 1s ease-in-out 0s;	
	box-shadow:none;
}
#experience .well:before {
    content: "";
    height: 100%;
    background-color: #fff;
    width: 2px;
    position: absolute;
    left: 0;
    top: 0;
}
#experience .well.left:before{
	right:-2px;
	left:auto;
}
#experience .well:after{
	content: '';
    background-color: #fff;
    width: 12px;
    height: 12px;
    position: absolute;
    top: 0;
    left: -5px;
    border-radius: 10px;
}
#experience .well.left:after{
	right:-7px;
	left:auto;
}
#experience .well h5{
	color: #0FB2D4;
}