*{
	margin:0;
	padding:0;
}
h1,h2,h3,h4,h5,h6,p,ul{
	margin:0;
	padding:0;
}
body {
  margin: 0;
  padding: 0;
  font-family: Roboto,"Helvetica Neue",sans-serif;
  font-size:1rem;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a{
	color:#5b5b5b;
	transition: all 1s ease-in-out 0s;
}
a:hover,
a:focus{
	color:#4BD0EA;
	transition: all 1s ease-in-out 0s;
}
hr{
	border: 2px solid #fff;
    width: 30px;
    margin: 20px auto;
}
.form-control::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #fff;
    opacity: 1; /* Firefox */
}
.form-control::placeholder { /* Internet Explorer 10-11 */
    color: #fff;
}
.form-control::placeholder { /* Microsoft Edge */
    color: #fff;
}
.homeTransition-appear{
  opacity: 0;
}
.homeTransition-appear.homeTransition-appear-active{
  opacity: 1;
  transition: all .4s ease-in-out;
}
.worksTransition-appear{
  opacity: 0;
  transform: translateX(-100px);
}
.worksTransition-appear.worksTransition-appear-active{
  opacity: 1;
  transform: translateX(0px);
  transition: all .4s ease-in-out;
}
.aboutTransition-appear{
  opacity: 0;
}
.aboutTransition-appear.aboutTransition-appear-active{
  opacity: 1;
  color: red;
  transition: all .4s ease-in-out;
}
@media(max-width:991px){
  body{
    font-size: 0.9rem;
  }
  .h1, h1{
    font-size:2rem;
  }
  .h2, h2 {
    font-size: 1.5rem;
  }
}
